import { PrestacaoServicoDto, SearchPrestacaoServicoDto } from './../../interfaces/servicos/prestacao-servico.dto';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { PageableDto, PageOptionsDto } from '../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';
import { API_CONFIG } from '../../../../config/api.config';
import { CreatePrestacaoServicoDto } from '../../interfaces/servicos/prestacao-servico.dto';

@Injectable({
  providedIn: 'root'
})
export class PrestacaoServicosService {


  constructor(
    private http: HttpClient,
  ) { }

  createFinalized(dto: CreatePrestacaoServicoDto): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/finalized`,
      dto,
      {
        responseType: 'json',
      });
  }

  create(dto: CreatePrestacaoServicoDto): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos`,
      dto,
      {
        responseType: 'json',
      });
  }

  updateFinalized(id: string, dto: CreatePrestacaoServicoDto): Observable<any> {
    return this.http.put<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/${id}/finalized`,
      dto,
      {
        responseType: 'json',
      });
  }

  update(id: string, dto: CreatePrestacaoServicoDto): Observable<any> {
    return this.http.put<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/${id}`,
      dto,
      {
        responseType: 'json',
      });
  }

  list(filterDto?: SearchPrestacaoServicoDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<PrestacaoServicoDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto) {
      if (filterDto.acrescimo_desconto) {
        params = params.append('acrescimo_desconto', filterDto.acrescimo_desconto);
      }
      if (filterDto.cliente_id) {
        params = params.append('cliente_id', filterDto.cliente_id);
      }
      if (filterDto.colaborador_id) {
        params = params.append('colaborador_id', filterDto.colaborador_id);
      }
      if (filterDto.created_at) {
        params = params.append('created_at', filterDto.created_at.toDateString());
      }
      if (filterDto.local) {
        params = params.append('local', filterDto.local);
      }
      if (filterDto.condominio_id) {
        params = params.append('condominio_id', filterDto.condominio_id);
      }
      if (filterDto.max_total) {
        params = params.append('max_total', filterDto.max_total);
      }
      if (filterDto.min_total) {
        params = params.append('min_total', filterDto.min_total);
      }
      if (filterDto.pagamento_forma) {
        params = params.append('pagamento_forma', filterDto.pagamento_forma);
      }
      if (filterDto.searchedCliente) {
        params = params.append('searchedCliente', filterDto.searchedCliente);
      }
      if (filterDto.searchedColaborador) {
        params = params.append('searchedColaborador', filterDto.searchedColaborador);
      }
      if (filterDto.searchedCondominio) {
        params = params.append('searchedCondominio', filterDto.searchedCondominio);
      }
      if (filterDto.searchedPrestacaoServico) {
        params = params.append('searchedPrestacaoServico', filterDto.searchedPrestacaoServico);
      }
      if (filterDto.status_servico) {
        params = params.append('status_servico', filterDto.status_servico);
      }
    }

    return this.http.get<PageableDto<PrestacaoServicoDto>>(`${API_CONFIG.baseURL}/prestacao/servicos`, {
      responseType: 'json',
      params,
    });
  }

  findById(id: string): Observable<PrestacaoServicoDto> {
    return this.http.get<PrestacaoServicoDto>(`${API_CONFIG.baseURL}/prestacao/servicos/${id}`, {
      responseType: 'json',
    });
  }

  initiateById(id: string): Observable<any> {
    return this.http.patch<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/${id}/initiate`,
      {
        responseType: 'json',
      });
  }

  finalizeById(id: string): Observable<any> {
    return this.http.patch<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/${id}/finalize`,
      {
        responseType: 'json',
      });
  }

  cancelById(id: string): Observable<any> {
    return this.http.delete<any>(
      `${API_CONFIG.baseURL}/prestacao/servicos/${id}`,
      {
        responseType: 'json',
      });
  }
}
