
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';

import { PrestacaoServicoDto } from './../../../../interfaces/servicos/prestacao-servico.dto';
import { LocalAgendamentoItens } from '../../../../constants/agendamento.constant';
import { Estados } from '../../../../constants/estado.constant';
import { StatusServico, StatusServicoEnum, StatusServicoWithNoCancelOption } from '../../../../constants/status.constant';
import { PrestacaoServicosService } from '../../../../services/prestacao-servicos/prestacao-servicos.service';
import { LocalAgendamento } from './../../../../../shared/constants/agendamento.constant';
import { PagamentoFormasMenu } from '../../../../constants/pagamento-formas.constant';
import { OpcoesAcrescimoDesconto, OpcoesAcrescimoDescontoEnum, OpcoesTipo, OpcoesTipoEnum } from '../../../../constants/descontos.constant';
import { ServicosDto } from '../../../../interfaces/servicos/servicoes.dto';
import { ServicosService } from '../../../../services/servico/servicos.service';
import { StorageService } from '../../../../services/auth/storage.service';
import { TipoUsuarioEnum } from '../../../../constants/tipo-user.constant';
import { UserDto } from '../../../../interfaces/users/user.dto';
import { PageOrder } from '../../../../constants/page.constant';
import { CondominioDto } from '../../../../interfaces/condominios/condominio.dto';
import { PageableDto } from '../../../../interfaces/others/pageable.dto';
import { CondominioService } from '../../../../services/condominio/condominio.service';
import { UserService } from '../../../../services/user/user.service';
import { ToastEnum, ToastPrimeSeverityEnum } from '../../../../constants/toast.constant';
import { ToastMessageService } from '../../../../services/toast/toast-message.service';
import { CreatePrestacaoServicoDto } from './../../../../../shared/interfaces/servicos/prestacao-servico.dto';
import { CreateItemPrestacaoServicoDto } from './../../../../../shared/interfaces/servicos/item-prestacao-servico.dto';
import { ServicoUtils } from './../../../../../shared/utils/ServicoUtils';


@Component({
  selector: 'app-servicos-update',
  templateUrl: './servicos-update.component.html',
  styleUrls: ['./servicos-update.component.scss'],
})
export class ServicosUpdateComponent implements OnInit, OnDestroy {

  @Input()
  prestacaoServico: PrestacaoServicoDto;

  form: UntypedFormGroup;
  menuAgendamento: MenuItem[];

  condominios: PageableDto<CondominioDto>;
  searchedcondominio = '';

  clients: PageableDto<UserDto>;
  searchedCliente = '';

  colaboradores: PageableDto<UserDto>;
  searchedColaborador = '';

  servicos: PageableDto<ServicosDto>;
  searchedServico = '';
  selectedServico: ServicosDto;
  showDialog = false;
  showStatusDialog = false;
  showAgendamento = false;
  showLocal = false;

  formasPagamento = PagamentoFormasMenu;
  selectedFormaPagamento;
  statusServico = StatusServicoWithNoCancelOption;
  selectedStatusServico = StatusServicoEnum.FINALIZADO;
  opcoesAcrescimoDesconto = OpcoesAcrescimoDesconto;
  selectedOpcaoAcrescimoDesconto = OpcoesAcrescimoDescontoEnum.NAO;
  opcoesTipo = OpcoesTipo;
  selectedOpcaoTipo;

  localAgendamentos = LocalAgendamentoItens;
  listEstados = Estados;
  liberateEndereco = false;

  total = 0;

  private subscriptions = new Subscription();

  constructor(
    private readonly formBuilder: UntypedFormBuilder,
    private readonly userService: UserService,
    private readonly condominioService: CondominioService,
    private readonly servicosService: ServicosService,
    private readonly modal: ModalController,
    private readonly storage: StorageService,
    private readonly toastService: ToastMessageService,
    private readonly prestacaoServicosService: PrestacaoServicosService,
  ) { }

  ngOnInit() {
    // Salvar direto não está funcionando porque não está pegando o item de prestação, testar troca de serviço

    this.menuAgendamento = [
      {
        label: 'Opções',
        items: [{
          label: 'Limpar',
          icon: 'pi pi-refresh',
          command: () => {
            this.resetAgendamento();
          }
        },
        ]
      },
    ];

    this.loadColaboradores();
    this.loadcondominios();
    this.loadClients();
    this.loadServicos();

    const {
      condominio, colaborador, cliente, agendamento_servico, itens_prestacao_servico,
      pagamento_forma, status_servico, acrescimo_desconto, descricao, local,
      total_devido,
    } = this.prestacaoServico;
    this.selectedStatusServico = status_servico;
    this.total = total_devido;

    this.form = this.formBuilder.group({
      condominio_id: [condominio.id, [Validators.required]],
      colaborador_id: [colaborador.id, [Validators.required,]],
      cliente_id: [cliente.id, [Validators.required,]],
      servico: [itens_prestacao_servico[0].servico.id, [Validators.required,]],
      pagamento_forma: [pagamento_forma, [Validators.required,]],
      acrescimo_desconto: [acrescimo_desconto, []],
      descricao: [descricao, []],
      quantidade: [itens_prestacao_servico[0].quantidade, [Validators.required, Validators.min(1)]],
      local: [local, [Validators.required]],

      data_inicio: [, []],
      data_fim: [, []],

      cep: [agendamento_servico.endereco?.cep, []],
      endereco: [agendamento_servico.endereco?.endereco, []],
      numero: [agendamento_servico.endereco?.numero, []],
      complemento: [agendamento_servico.endereco?.complemento, []],
      bairro: [agendamento_servico.endereco?.bairro, []],
      cidade: [agendamento_servico.endereco?.cidade, []],
      estado: [agendamento_servico.endereco?.estado, []],
    });

    if (this.selectedStatusServico == StatusServicoEnum.AGENDADO) {
      this.showAgendamento = true;
      this.form.get('data_inicio').setValue(new Date(agendamento_servico.compromisso.data_inicio_completa));
      this.form.get('data_fim').setValue(new Date(agendamento_servico.compromisso.data_fim_completa))

      this.form.get('data_inicio').setValidators([Validators.required]);
      this.form.get('data_fim').setValidators([Validators.required]);

      this.form.get('data_inicio').updateValueAndValidity();
      this.form.get('data_fim').updateValueAndValidity();
    }
    this.localChanged();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  //#region condominio
  loadcondominios(page = 1, dropdown?: Dropdown) {
    const sub = this.condominioService.list(
      { searchedCondominio: this.searchedcondominio },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.condominios = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  paginarcondominio(event) {
    this.loadcondominios();
  }
  oncondominioFilter(event) {
    const filter = event.filter as string;
    this.searchedCliente = filter;
  }

  blurcondominio(event, dropdown: Dropdown) {
    this.searchedCliente = undefined;
    dropdown.filterValue = undefined;
  }
  //#endregion

  //#region CLIENTE
  loadClients(page = 1) {
    const sub = this.userService.listUsers(
      { searchedUser: this.searchedCliente, tipo_usuario: TipoUsuarioEnum.CLIENTE },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.clients = response;
      });
    this.subscriptions.add(sub);
  }
  paginarClient(event) {
    this.loadClients();
  }
  onClientFilter(event) {
    const filter = event.filter as string;
    this.searchedCliente = filter;
  }
  blurClient(event, dropdown: Dropdown) {
    this.searchedCliente = undefined;
    dropdown.filterValue = undefined;
  }
  //#endregion

  //#region COLABORADOR
  loadColaboradores(page = 1) {
    const sub = this.userService.listUsers(
      { searchedUser: this.searchedColaborador, tipo_usuario: TipoUsuarioEnum.FUNCIONARIO },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.colaboradores = response;
      });
    this.subscriptions.add(sub);
  }
  paginarColaborador(event) {
    this.loadColaboradores();
  }
  onColaboradorFilter(event) {
    const filter = event.filter as string;
    this.searchedColaborador = filter;
  }
  blurColaborador(event, dropdown: Dropdown) {
    this.searchedColaborador = undefined;
    dropdown.filterValue = undefined;
  }
  //#endregion

  //#region SERVICOS
  loadServicos(page = 1, dropdown?: Dropdown) {
    const sub = this.servicosService.list(
      { searchedServico: this.searchedServico },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.servicos = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  paginarServico(event) {
    this.loadServicos();
  }
  onServicoFilter(event) {
    const filter = event.filter as string;
    this.searchedServico = filter;
  }
  blurServico(event, dropdown: Dropdown) {
    this.searchedServico = undefined;
    dropdown.filterValue = undefined;
  }
  onServicoChange({ value }) {
    if (!value) {
      this.total = 0;
      return;
    }
    const selectedServicoId = this.form.get('servico').value;
    const servico = this.servicos.data.find(s => s.id == selectedServicoId);

    const { preco_servico } = servico;
    const quantidade = this.form.get('quantidade').value;
    this.total = Number(preco_servico) * Number(quantidade);
  }
  detailServico() {
    const selectedServicoId = this.form.get('servico').value;
    this.selectedServico = this.servicos.data.find(s => s.id == selectedServicoId);
    if (!this.selectedServico) {
      this.toastService.presentToast({
        detalhe: `Selecione um serviço primeiro`,
        titulo: `Seleção necessária`,
        duracao: ToastEnum.shortDuration,
        gravidade: ToastPrimeSeverityEnum.INFORMACAO,
      });
      return;
    }
    this.showDialog = true;
  }
  changeQuantidade() {
    const selectedServicoId = this.form.get('servico').value;
    const servico = this.servicos.data.find(s => s.id == selectedServicoId);

    if (!servico) {
      this.total = 0;
      return;
    }
    const { preco_servico } = servico;
    const quantidade = this.form.get('quantidade').value;
    this.total = Number(preco_servico) * Number(quantidade);

  }
  //#endregion

  //#region AGENDAMENTO
  changeStatusServico(event?: any) {
    if (this.selectedStatusServico == StatusServicoEnum.AGENDADO) {
      this.showAgendamento = true;
      this.form.get('data_inicio').setValue(new Date());
      this.form.get('data_fim').setValue(undefined)

      this.form.get('data_inicio').setValidators([Validators.required]);
      this.form.get('data_fim').setValidators([Validators.required]);

      this.form.get('data_inicio').updateValueAndValidity();
      this.form.get('data_fim').updateValueAndValidity();
      return;
    }

    this.form.get('local').setValue(LocalAgendamento.LOCAL);
    this.form.get('data_inicio').clearValidators();
    this.form.get('data_fim').clearValidators();
    this.form.get('data_inicio').updateValueAndValidity();
    this.form.get('data_fim').updateValueAndValidity();
    this.hideEnderecoFields();

    this.showAgendamento = false;
    this.form.updateValueAndValidity({ onlySelf: true });
  }

  resetAgendamento() {
    this.form.get('local').setValue(LocalAgendamento.LOCAL);

    this.form.get('data_inicio').setValue(new Date());
    this.form.get('data_fim').setValue(undefined);

    this.form.get('data_inicio').clearValidators();
    this.form.get('data_fim').clearValidators();

    this.form.get('cep').setValue(undefined);
    this.form.get('endereco').setValue(undefined);
    this.form.get('numero').setValue(undefined);
    this.form.get('complemento').setValue(undefined);
    this.form.get('bairro').setValue(undefined);
    this.form.get('cidade').setValue(undefined);
    this.form.get('estado').setValue(undefined);
    this.hideEnderecoFields();
    this.form.updateValueAndValidity({ onlySelf: true });

  }

  localChanged() {
    const local = this.form.get('local').value;

    if (local == LocalAgendamento.EXTERNO) {
      this.showEnderecoFields();
    } else {
      this.hideEnderecoFields();
    }

  }
  //#endregion

  //#region FECHAMENTO
  getSubTotal() {
    return this.total;
  }

  getTotal() {
    return Number(this.total) + Number(this.getDescontoAcrescimo());
  }
  disableOpcoesTipo() {
    return this.selectedOpcaoAcrescimoDesconto && this.selectedOpcaoAcrescimoDesconto != OpcoesAcrescimoDescontoEnum.NAO;
  }
  listenOpcaoAcrescimoDesconto() {
    if (this.selectedOpcaoAcrescimoDesconto == OpcoesAcrescimoDescontoEnum.NAO) {
      this.selectedOpcaoTipo = undefined;
      this.form.get('acrescimo_desconto').setValue(undefined);

      this.form.get('acrescimo_desconto').clearValidators();
      this.form.get('descricao').clearValidators();
    } else {
      this.form.get('acrescimo_desconto').setValidators([Validators.required]);
      this.form.get('descricao').setValidators([Validators.required, Validators.minLength(5)]);
    }

    this.form.get('acrescimo_desconto').updateValueAndValidity();
    this.form.get('descricao').updateValueAndValidity();
  }
  listenOpcoesTipoChange() {
    this.form.get('acrescimo_desconto').setValue(undefined);
  }
  getDescontoAcrescimo() {
    let acrescimo_desconto = this.form.get('acrescimo_desconto').value;
    if (!acrescimo_desconto || acrescimo_desconto == 0) {
      return 0;
    }

    if (this.selectedOpcaoTipo == OpcoesTipoEnum.PERCENTUAL) {
      acrescimo_desconto = this.total * (acrescimo_desconto / 100);
    }

    if (this.selectedOpcaoAcrescimoDesconto == OpcoesAcrescimoDescontoEnum.DESCONTO) {
      acrescimo_desconto = acrescimo_desconto * -1;
    }

    return Number(acrescimo_desconto);
  }
  getDescontoAcrescimoTxt() {
    const valor = this.getDescontoAcrescimo();
    if (!valor || valor == 0) {
      return ' -';
    }
    return valor < 0 ? `(-) ${(valor * -1)}` : `(+) ${valor}`;
  }
  //#endregion

  update() {

    const { id } = this.prestacaoServico;
    const cliente_id = this.form.get('cliente_id').value;
    const colaborador_id = this.form.get('colaborador_id').value;
    const condominio_id = this.form.get('condominio_id').value;

    const servico_id = this.form.get('servico').value;
    const quantidade = Number(this.form.get('quantidade').value);

    const descricao = this.form.get('descricao').value;
    const acrescimo_desconto = this.form.get('acrescimo_desconto').value;
    const pagamento_forma = this.form.get('pagamento_forma').value;
    const local = this.form.get('local').value;

    const itens_prestacao_servico: CreateItemPrestacaoServicoDto[] = [{
      servico_id,
      quantidade,
    }]

    let prestacao_servico: CreatePrestacaoServicoDto = {
      cliente_id,
      colaborador_id,
      descricao,
      condominio_id,
      pagamento_forma,
      itens_prestacao_servico,
      local,
    }

    prestacao_servico = ServicoUtils.setAcrescimoDesconto(
      this.selectedOpcaoAcrescimoDesconto,
      this.selectedOpcaoTipo,
      prestacao_servico,
      acrescimo_desconto
    );

    if (this.selectedStatusServico == StatusServicoEnum.FINALIZADO) {
      this.prestacaoServicosService.updateFinalized(id, prestacao_servico)
        .subscribe(data => {
          this.toastService.presentToast({
            detalhe: `Sucesso`,
            titulo: `Prestação de serviço registrada com sucesso`,
            duracao: ToastEnum.mediumDuration,
            gravidade: ToastPrimeSeverityEnum.SUCESSO,
          });
          this.close(true);
        });

      return;
    }

    if (this.selectedStatusServico == StatusServicoEnum.INICIADO) {
      this.prestacaoServicosService.update(id, prestacao_servico)
        .subscribe(data => {
          this.toastService.presentToast({
            detalhe: `Sucesso`,
            titulo: `Prestação de serviço iniciada com sucesso`,
            duracao: ToastEnum.mediumDuration,
            gravidade: ToastPrimeSeverityEnum.SUCESSO,
          });
          this.close(true);
        });

      return;
    }

    if (this.selectedStatusServico == StatusServicoEnum.AGENDADO) {
      this.prestacaoServicosService.update(id, prestacao_servico)
        .subscribe(data => {
          this.toastService.presentToast({
            detalhe: `Sucesso`,
            titulo: `Prestação de serviço agendada com sucesso`,
            duracao: ToastEnum.mediumDuration,
            gravidade: ToastPrimeSeverityEnum.INFORMACAO,
          });
          this.close(true);
        });

      return;
    }


  }

  close(hasUpdate = false) {
    this.modal.dismiss(hasUpdate);
  }

  //#region INPUT-CTRL
  isInputError(inputName: string): boolean {
    const resp =
      !this.form.controls[inputName].untouched &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableCreateBtn(): boolean {
    const situacao = !this.form.valid;
    return situacao;
  }

  clearForm() {
    this.form.reset();

    this.selectedFormaPagamento = undefined;
    this.selectedOpcaoAcrescimoDesconto = undefined;
    this.selectedOpcaoTipo = undefined;
    this.total = undefined;

    this.resetAgendamento();
    this.showAgendamento = false;
    this.selectedStatusServico = StatusServicoEnum.FINALIZADO;
    this.selectedOpcaoAcrescimoDesconto = OpcoesAcrescimoDescontoEnum.NAO;

    const { condominio, id: colaborador_id } = this.storage.getCompleteLocalUser();
    this.form.get('condominio_id').setValue(condominio.id);
    this.form.get('colaborador_id').setValue(colaborador_id);
    this.form.get('quantidade').setValue(1);
    this.form.get('local').setValue(LocalAgendamento.LOCAL);

    this.form.updateValueAndValidity({ onlySelf: true });

  }

  private generateAgendamentoServicoObject() {
    const data_inicio = this.form.get('data_inicio').value;
    const data_fim = this.form.get('data_fim').value;
    let endereco_compromisso = undefined;
    const local = this.form.get('local').value;
    if (local == LocalAgendamento.EXTERNO) {
      const cep = this.form.get('cep').value;
      const endereco = this.form.get('endereco').value;
      const numero = this.form.get('numero').value;
      const complemento = this.form.get('complemento').value;
      const bairro = this.form.get('bairro').value;
      const cidade = this.form.get('cidade').value;
      const estado = this.form.get('estado').value;

      endereco_compromisso = {
        cep,
        endereco,
        numero,
        complemento,
        bairro,
        cidade,
        estado,
      }
    }

    return {
      data_inicio,
      data_fim,
      endereco_compromisso,
    }
  }

  private showEnderecoFields() {
    this.form.get('cep').setValidators([Validators.required]);
    this.form.get('endereco').setValidators([Validators.required]);
    this.form.get('numero').setValidators([Validators.required]);
    this.form.get('complemento').setValidators([Validators.required]);
    this.form.get('bairro').setValidators([Validators.required]);
    this.form.get('cidade').setValidators([Validators.required]);
    this.form.get('estado').setValidators([Validators.required]);
    this.form.updateValueAndValidity({ onlySelf: true });
    this.liberateEndereco = true;
  }

  private hideEnderecoFields() {
    this.form.get('cep').clearValidators();
    this.form.get('cep').setErrors(null);
    this.form.get('endereco').clearValidators();
    this.form.get('endereco').setErrors(null);
    this.form.get('numero').clearValidators();
    this.form.get('numero').setErrors(null);
    this.form.get('complemento').clearValidators();
    this.form.get('complemento').setErrors(null);
    this.form.get('bairro').clearValidators();
    this.form.get('bairro').setErrors(null);
    this.form.get('cidade').clearValidators();
    this.form.get('cidade').setErrors(null);
    this.form.get('estado').clearValidators();
    this.form.get('estado').setErrors(null);
    this.form.updateValueAndValidity({ onlySelf: true });
    this.liberateEndereco = false;
  }
  //#endregion
}
